.card {
  display: inline-flex;
  justify-content: stretch;
  align-items: center;
  flex-direction: column;
  width: 350px;
  max-width: calc(100% - 20px - 20px);
  margin: 20px;
  background: var(--background);
  border-radius: var(--rounded);
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  vertical-align: top;
  transition: all 0.3s ease;
  border: 1px solid rgba(7, 149, 217, 0.1);
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--primary), var(--secondary));
    transform: scaleX(0);
    transition: transform 0.3s ease;
    z-index: 10;
  }

  &:hover {
    transform: translateY(-8px);
    box-shadow: 0 12px 30px rgba(7, 149, 217, 0.2);

    &::before {
      transform: scaleX(1);
    }

    .card-image img {
      transform: scale(1.05);
    }
  }
}

.card[data-style="small"] {
  width: 250px;
}

.card-image {
  overflow: hidden;
}

.card-image img {
  aspect-ratio: 3 / 2;
  object-fit: cover;
  width: 100%;
  transition: transform 0.3s ease;
}

.card-text {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  gap: 15px;
  max-width: 100%;
  padding: 25px;
}

.card-text > * {
  margin: 0 !important;
}

.card-title {
  font-family: var(--heading);
  font-weight: var(--semi-bold);
  color: var(--primary);
  font-size: 1.1rem;
}

.card-subtitle {
  margin-top: 0 !important;
  font-style: italic;
  color: var(--gray);
  font-size: 0.95rem;
}
